import uniRate from '@/components/uni-ui/uni-rate/uni-rate.vue';
export default {
	components: { uniRate },
	data() {
		return {
			orderItemId: 0,
			textareaBValue: '',
			imgList: [],
			attachment: [],
			valueSync: 5
		};
	},
	onLoad(e) {
		this.orderItemId = e.orderItemId
	},
	onShow() {},
	methods: {
		textareaBInput(e) {
			this.textareaBValue = e.detail.value;
		},
		chooseImage() {
			uni.chooseImage({
				count: 4,
				sizeType: ['original', 'compressed'],
				sourceType: ['album'],
				success: res => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths);
					} else {
						this.imgList = res.tempFilePaths;
					}
					//res.tempFilePaths是数组
					res.tempFilePaths.forEach(item => {
						uni.uploadFile({
							url: 'http://localhost:8085/admin/comment/image/upload',
							filePath: item,
							name: 'file',
							success: res => {
								console.log(JSON.parse(res.data).data);

								this.attachment.push(JSON.parse(res.data).data);
							}
						});
					});
				}
			});
		},
		viewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		deleteImage(e) {
			uni.showModal({
				content: '确定要删除此图片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1);
					}
				}
			});
		},
		getLevel(level) {
			this.valueSync = level.value
		},
		comment() {
			uni.showLoading();
			if(this.textareaBValue === ''){
				return uni.showToast({
					title:'评价内容不能为空'
				})
			}
			this.$H.post('/product/comment',{
				orderItemId:this.orderItemId,//this.orderItemId,
				comment: this.textareaBValue,
				starLevel: this.valueSync,
				images: this.attachment,
			},{
				token:true
			}).then(res => {
				uni.showToast({
					title:'评价成功',
					icon:'none'
				})
				uni.navigateBack()
			})
		}
	}
};
